.header {
  background-color: #a03897;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5%;
  height: 50px;
}

.header p {
  color: rgba(240, 248, 255, 0.781);
}

.icons {
  display: flex;
  list-style-type: none;
}

.icons li:last-child {
  margin-left: 0.8rem;
}
